import { Avatar, Card, Col, Row, Skeleton } from "antd";
import RegiondataWidget from "components/shared-components/RegiondataWidget";
import StatisticWidget from "components/shared-components/StatisticWidget";
import React from "react";
import { useState } from "react";
import { useDispatch } from "react-redux";
import { useSelector } from "react-redux";
import { getRegionalData, setAllocationData, setGlobalTrend, setRegionData, setRegionDescData } from "redux/actions";
import dashboardServices from "services/DashboardServices";

function RegionalWidgetComp() {
  const dispatch = useDispatch();
  const [loader, setLoader] = useState(false);
  const globalTrends = useSelector(state => state.myTeams.globalTrends);
  const [flag, setFlag] = useState("global");
  const globalFlag = useSelector(state => state.myTeams?.regionDesc?.flag);

  const getData = async params => {
    setFlag(params.location);
    if (flag !== params.location) {
      try {
        setLoader(true);
        const response = await dashboardServices.getRegionalData(params);
        const { headCountTrends, allocations, regions, count, description, title } = response;
        let regionDesc = { count, description, title, flag: params.location };
        dispatch(setGlobalTrend(headCountTrends));
        dispatch(setRegionData(regions));
        dispatch(setAllocationData(allocations));
        dispatch(setRegionDescData(regionDesc));
      } catch (e) {
        console.error("Unable to populate Allocation list", e);
      } finally {
        setLoader(false);
      }
    }
  };

  const getFlag = key => {
    switch (key) {
      case "United States":
        return <Avatar size={30} src="/img/flags/america.gif" />;
      case "Canada":
        return <Avatar size={30} src="/img/flags/canada.gif" />;
      case "Chile":
        return <Avatar size={30} src="/img/flags/chile.gif" />;
      case "Pakistan":
        return <Avatar size={30} src="/img/flags/pakistan.gif" />;
      case "India":
        return <Avatar size={30} src="/img/flags/india.gif" />;
      case "Colombia":
        return <Avatar size={30} src="/img/flags/columbia.gif" />;
      default:
        return <Avatar size={30} src="/img/flags/global.png" />;
    }
  };
  return (
    <React.Fragment>
      <Card>
        <Row gutter={16}>
          {!!globalTrends.length && !loader
            ? globalTrends.map((elm, i) => (
                <Col xs={24} sm={24} md={8} lg={6} xl={6} key={i} className="gutter-row">
                  <StatisticWidget title={elm.title} value={elm.value} status={elm.status} subtitle={elm.subTitle} />
                </Col>
              ))
            : [...Array(5).keys()].map(e => {
                return (
                  <Col xs={24} sm={24} md={12} lg={4} xl={4} key={e}>
                    <Skeleton id={e} active />
                  </Col>
                );
              })}
        </Row>
        <RegiondataWidget onChange={getData} flag={getFlag(globalFlag)} loader={loader} />
      </Card>
    </React.Fragment>
  );
}

export default RegionalWidgetComp;
