import React, { useState, useEffect } from "react";
import { Card, Row, Col, Badge, Grid, Skeleton, Button } from "antd";
import { SyncOutlined } from "@ant-design/icons";
import PropTypes from "prop-types";
import { ComposableMap, Geographies, Geography } from "react-simple-maps";
import ReactTooltip from "react-tooltip";
import WorldMap from "assets/maps/world-countries-sans-antarctica.json";
import utils from "utils";
import Flex from "../Flex";
import DonutChartWidget from "../DonutChartWidget";
import { useDispatch, useSelector } from "react-redux";
import { setGlobalMapData } from "redux/actions";
import { COLORS } from "constants/ChartConstant";

const { useBreakpoint } = Grid;
const geoUrl = WorldMap;
const mapColor = "#F5F4F6";
const hoverPercentage = -10;

const getHighlightedRegion = (name, data) => {
  if (data.length > 0 || name) {
    for (let i = 0; i < data.length; i++) {
      const elm = data[i];
      if (name === elm.name) {
        return elm.color;
      }
    }
    return mapColor;
  }
  return mapColor;
};

const getRegionHoverColor = (name, data) => {
  if (data.length > 0 || name) {
    for (let i = 0; i < data.length; i++) {
      const elm = data[i];
      if (name === elm.name) {
        return utils.shadeColor(elm.color, hoverPercentage);
      }
    }
    return utils.shadeColor(mapColor, hoverPercentage);
  }
  return utils.shadeColor(mapColor, hoverPercentage);
};

const getRegionValue = (name, data) => {
  if (data.length > 0 || name) {
    for (let i = 0; i < data.length; i++) {
      const elm = data[i];
      if (name === elm.name) {
        return `${elm.name} â€” ${elm.value}`;
      }
    }
    return "";
  }
  return "";
};

const MapChart = ({ setTooltipContent, data, mapSource, mapType, handleOnChangeRegion }) => {
  const handleChange = geoName => {
    let selectedCountries = data.map(e => (e.title === "United States" ? "United States of America" : e.title));
    if (selectedCountries.includes(geoName)) {
      let param = {
        location: geoName,
        account: "All",
      };
      if (geoName === "United States of America") {
        param.location = "United States";
        handleOnChangeRegion(param);
        return;
      }
      handleOnChangeRegion(param);
    }
  };
  return (
    <ComposableMap
      style={{ transform: `${mapType === "world" ? "translateY(20px)" : "none"}` }}
      data-tip=""
      height={380}
      projectionConfig={{ scale: 145 }}
    >
      <Geographies geography={mapSource}>
        {({ geographies }) =>
          geographies.map(geo => {
            const geoName = mapType === "world" ? geo.properties.name : geo.properties.NAME_1;
            return (
              <Geography
                key={geo.rsmKey}
                geography={geo}
                onClick={() => handleChange(geoName)}
                onMouseEnter={() => {
                  setTooltipContent(getRegionValue(geoName, data));
                }}
                onMouseLeave={() => {
                  setTooltipContent("");
                }}
                fill={getHighlightedRegion(geoName, data)}
                stroke="#D6D6DA"
                style={{
                  hover: {
                    fill: getRegionHoverColor(geoName, data),
                    outline: "none",
                  },
                }}
              />
            );
          })
        }
      </Geographies>
    </ComposableMap>
  );
};

const Map = props => {
  const { data, mapSource, mapType, onChange } = props;
  const [content, setContent] = useState("");
  return (
    <>
      <MapChart
        data={data}
        mapSource={mapSource}
        mapType={mapType}
        setTooltipContent={setContent}
        handleOnChangeRegion={onChange}
      />
      <ReactTooltip>{content}</ReactTooltip>
    </>
  );
};

const renderDataList = (data, loader) => {
  const list =
    !!data.length && !loader ? (
      data.map(elm => (
        <div className="d-flex align-items-center justify-content-between mb-3" key={elm.name}>
          <div>
            <Badge color={elm.color} />
            <span className="text-gray-light">{elm.title}</span>
          </div>
          <span className="font-weight-bold text-dark">{elm.value}</span>
        </div>
      ))
    ) : (
      <Skeleton active />
    );
  return list;
};

export const RegiondataWidget = props => {
  const { mapSource, mapType, list, onChange, flag, loader } = props;
  const [allocationData, setAllocationData] = useState([]);
  const [allocationLabel, setAllocationLabel] = useState([]);

  const regionsDesc = useSelector(state => state.myTeams.regionDesc);
  const regions = useSelector(state => state.myTeams.regionData);
  const allocations = useSelector(state => state.myTeams.allocationData);
  const globalMap = useSelector(state => state.myTeams.globalMapData);
  const [mapRegions, setMapRegions] = useState([]);
  const dispatch = useDispatch();

  useEffect(() => {
    if (!!regions.length) {
      let newRegions = prepareRegionsColors(regions);
      setMapRegions(newRegions);
      if (globalMap.length === 0) {
        dispatch(setGlobalMapData(newRegions));
      }
    }
    if (allocations) {
      setAllocationData(allocations?.allocationData?.map(Number));
      setAllocationLabel(allocations?.allocationLabels);
    }
  }, [allocations, regions, globalMap]);

  const prepareRegionsColors = regions => {
    const newRegions = regions.map((i, index) => {
      return {
        ...i,
        name: i.title === "United States" ? "United States of America" : i.title,
        color: COLORS[index],
      };
    });
    return newRegions;
  };

  const isMobile = !utils.getBreakPoint(useBreakpoint()).includes("lg");
  const dataForChart = () => {
    let arr = [];
    for (let i = 0; i < allocationData?.length; i++) {
      const data = allocationData[i];
      const label = allocationLabel[i];
      const color = COLORS[i];
      arr = [
        ...arr,
        {
          data: data,
          label: label,
          color: color,
        },
      ];
    }
    return arr;
  };

  const newData = dataForChart();

  const refreshData = () => {
    onChange({
      location: "All",
      account: "All",
    });
  };

  return (
    <Card bodyStyle={{ padding: 0 }} className="mb-0">
      <Row>
        <Col xs={24} sm={24} md={24} lg={6} className="border-right">
          <div className="d-flex flex-column p-3 h-100" style={{ justifyContent: "space-evenly" }}>
            {loader ? (
              <Skeleton />
            ) : (
              <div>{regionsDesc && <h4 className="font-weight-bold">{regionsDesc?.title}</h4>}</div>
            )}

            <div>
              <div className="mb-4">
                <div className="d-flex align-items-center justify-content-between">
                  {regionsDesc && !loader ? (
                    <>
                      <div className="d-flex">
                        {flag}
                        <h2 className="mb-0 ml-2 font-weight-bold">{regionsDesc && regionsDesc?.count}</h2>
                      </div>
                      <div>
                        <Button onClick={refreshData}>
                          <SyncOutlined shape="circle" />
                        </Button>
                      </div>
                    </>
                  ) : (
                    <Skeleton.Button active={true} />
                  )}
                </div>
                {loader ? <Skeleton /> : <span className="text-muted">{regionsDesc && regionsDesc?.description}</span>}
              </div>
            </div>
            <div>{list ? list : renderDataList(mapRegions, loader)}</div>
          </div>
        </Col>
        <Col xs={24} sm={24} md={24} lg={12}>
          <div className="d-flex flex-column justify-content-center" style={{ minHeight: isMobile ? 200 : 435 }}>
            <div className="p-3 w-100">
              {loader ? (
                <Skeleton />
              ) : (
                <Map data={globalMap} mapSource={mapSource} mapType={mapType} onChange={onChange} />
              )}
            </div>
          </div>
        </Col>
        <Col xs={24} sm={24} md={24} lg={6} className="border-left">
          <DonutChartWidget
            series={allocationData}
            labels={allocationLabel}
            title="Allocation Status"
            bodyClass="my-3"
            customOptions={{ colors: COLORS }}
            loader={loader}
            extra={
              <Row justify="center">
                <Col xs={20} sm={20} md={20} lg={24}>
                  <div className="mt-4 mx-auto" style={{ maxWidth: 200 }}>
                    {!!newData.length && !loader ? (
                      newData?.map(elm => (
                        <Flex alignItems="center" justifyContent="between" className="mb-3" key={elm.label}>
                          <div>
                            <Badge color={elm.color} />
                            <span className="text-gray-light">{elm.label}</span>
                          </div>
                          <span className="font-weight-bold text-dark">{elm.data}</span>
                        </Flex>
                      ))
                    ) : (
                      <Skeleton />
                    )}
                  </div>
                </Col>
              </Row>
            }
          />
        </Col>
      </Row>
    </Card>
  );
};

RegiondataWidget.propTypes = {
  title: PropTypes.string,
  data: PropTypes.array,
  mapSource: PropTypes.object,
  mapType: PropTypes.string,
  content: PropTypes.element,
  list: PropTypes.element,
};

RegiondataWidget.defaultProps = {
  data: [],
  mapSource: geoUrl,
  mapType: "world",
};

export default RegiondataWidget;
